import { Component, OnInit, Input } from "@angular/core";

@Component({
  selector: "charts",
  templateUrl: "./charts.component.html",
  styleUrls: ["./charts.component.css"],
})
export class ChartsComponent implements OnInit {
  @Input() data: any[] = [];
  @Input() colors: number = null;
  possibleColors = ["#ff0000", "#bdbf21", "#53d46c", "#076e01"];

  charts = [
    {
      labels: ["MS"],
      data: [20, 80],
      backgroundColor: ["#ff6384", "#cc65fe"],
      type: "doughnut",
    },
    {
      labels: ["FDN"],
      data: [50, 50],
      type: "doughnut",
    },
    {
      labels: ["Amido"],
      data: [10, 90],
      type: "doughnut",
    },
    {
      labels: ["NDT"],
      data: [75, 25],
      type: "doughnut",
    },
    {
      labels: ["Produção de Leite"],
      data: [30, 70],
      type: "doughnut",
    },
    {
      labels: ["KPS"],
      data: [30, 70],
      type: "doughnut",
    },
  ];

  chartOptions = {
    cutoutPercentage: 80,
    responsive: true,
    maintainAspectRatio: false,
    legend: {
      display: true,
      align: "start",
      labels: {
        fontColor: "rgb(255, 99, 132)",
        boxWidth: 20,
      },
    },
  };
  backgroundColor: ["red", "blue"];

  current = 25;
  max = 100;

  constructor() {}

  ngOnInit(): void {
    console.log(this.colors);
  }
  returnIndex(index: number) {
    let color = "";
    switch (index) {
      case 0:
        color = this.colorMs(this.data[index]);
        break;
      case 1:
        color = this.colorFDN(this.data[index]);
        break;
      case 2:
        color = this.colorAmido(this.data[index]);
        break;
      case 3:
        color = this.colorNDT(this.data[index]);
        break;
      case 4:
        color = this.colorLeite(this.data[index]);
        break;
      case 5:
        color = this.colorKPS(this.data[index]);
        break;

      default:
        color = "#33333";
        break;
    }
    return color;
  }
  colorMs(value) {
    if (value <= 24 || value > 45) {
      return this.possibleColors[0];
    }
    if ((value > 24 && value < 27) || (value >= 38 && value <= 41)) {
      return this.possibleColors[1];
    }
    if ((value >= 27 && value < 30) || (value >= 35 && value < 38)) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
  colorFDN(value) {
    if (value >= 46) {
      return this.possibleColors[0];
    }
    if (value >= 40 && value < 46) {
      return this.possibleColors[1];
    }
    if (value >= 35 && value < 40) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
  colorAmido(value) {
    if (value <= 29) {
      return this.possibleColors[0];
    }
    if (value > 29 && value <= 35) {
      return this.possibleColors[1];
    }
    if (value > 35 && value <= 40) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
  colorNDT(value) {
    if (value < 68) {
      return this.possibleColors[0];
    }
    if (value >= 68 && value < 71) {
      return this.possibleColors[1];
    }
    if (value >= 71 && value <= 74) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
  colorKPS(value) {
    if (value < 55) {
      return this.possibleColors[0];
    }
    if (value >= 55 && value < 60) {
      return this.possibleColors[1];
    }
    if (value >= 60 && value < 65) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
  colorLeite(value) {
    if (value < 1.43) {
      return this.possibleColors[0];
    }
    if (value >= 1.43 && value < 1.55) {
      return this.possibleColors[1];
    }
    if (value >= 1.55 && value < 1.65) {
      return this.possibleColors[2];
    } else {
      return this.possibleColors[3];
    }
  }
}
